/********************* STORY *********************/
h2 {
	color: var(--text-color);
	font: 2.5em "Playfair Display", serif;
	font-weight: 400;
	font-style: normal;
	margin-top: 0;
}

.story {
	border-bottom: 1px solid var(--divider-color);
	padding: calc(var(--base-unit) * 3) 0;
}
.story:last-child {
	border-bottom: 0;
}


