/********************* MASTHEAD *********************/
h1 {
	color: var(--text-color);
	font: 4em "Playfair Display", serif;
	font-weight: 400;
	font-style: normal;
	margin: calc(var(--base-unit) * 2) 0 calc(var(--base-unit) * 3);
}

.masthead {
	text-align: center;
	border-bottom: 1px solid var(--text-color);
}

.subheader {
	font: 1em "Lato", sans-serif;
	padding: 0;
	margin-bottom: 0;
}

.subheader--item {
	border-right: 1px solid var(--text-color);
	display: inline;
	list-style: none;
	padding: 0 var(--base-unit) 2px;
}

.subheader--item:last-child {
	border-right: 0;
}


